export default {
	data() {
		return {
			id:"",
			result:null
		}
	},
	onLoad(options) {
		let that = this;
		that.id = options.id;			
		that.detail(options.id);
	},
	methods: {
		detail(id){
			const that = this;
			let param = {
				id: id
			}
			that.$api.dailyDetail(param).then(res => {
				if (res.success) {
					let result = res.datas;
					result.headImgUrl = result.headImgUrl?result.headImgUrl:"../../../../static/images/defaultUserPic.png";
					that.result = result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoProcess(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/process/index",
				query:{
					id:id
				}
			})
		},
		gotoAdd(){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/journal/add"
			})
		},
		gotoSelfPage(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/otherSelfPage",
				query:{
					id:id
				}
			})
		}
	}
}
